.garantee {
    justify-content: space-between;
    @media (max-width: 768px) {
        &.block-main {
            flex-direction: row;
            flex-wrap: wrap;
        }
    } 
    &-head {
        font-style: normal;
        font-weight: normal;
        font-size: 20px;
        line-height: 23px;
        
        margin-bottom: 35px;
    }
    .text {
        font-style: normal;
        font-weight: 300;
        font-size: 16px;
        line-height: 21px;
        text-align: justify;
        
        margin-bottom: 60px;
            & > * {
                margin-bottom: 25px;
            }
    }
    .contact {
        &-in {
            width: 45%;
            @media (max-width:850px) {
                width: 48%;
            }
            @media (max-width: 600px) {
                width: 100%; 
            }
        }
    }
    &__form {
        margin-bottom: 95px;
        max-width: 540px;
        margin-left: auto;
        margin-right: auto;
        &-h1 {
            text-align: center;
            
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 19px;
            
            margin-bottom: 28px;
        }
        &-h2 {
            text-align: center;
            
            font-style: normal;
            font-weight: 300;
            font-size: 14px;
            line-height: 20px;
            
            margin-bottom: 42px;
            color: #272727;
            &-a {
                text-decoration: none;
                color: #272727;
            }
        }
        &-input {
            font-style: normal;
            font-weight: 300;
            font-size: 14px;
            line-height: 20px;
            
            background: #fff;
            border-radius: 5px;
            
            border: none;
            outline: none;
            
            display: block;
            width: 100%;
            
            margin-bottom: 13px;
            
            padding: 12px 15px;
        }
        &-textarea {
            min-height: 95px;
        }
        &-border {
            border: 1px solid #EDEEF2
        }
        .flex {
            justify-content: space-between;
            .garantee__form-input {
                width: 49%;
            }
        }
        &-span {
            font-style: normal;
            font-weight: 300;
            font-size: 12px;
            line-height: 14px;
            margin-bottom: 28px;
            
            color: $grey;
        }
    }   
}