.catalog {
    &-top {
        padding: 5px 10px 30px 10px;
        justify-content: space-between;
        align-items: center;
        @media (max-width:915px) {
            flex-wrap: wrap;
            .block-head {
                width: 100%;
                font-size: 20px;
                margin: 30px 0;
            }
            
            .jq-selectbox {
                vertical-align: middle;
                cursor: pointer;
                width: 48%;
            }
   
        }
    }
    &-left {
        width: 265px;
        min-width: 265px;
        margin-right: 23px;
        @media (max-width:915px) {
            position: absolute;
            left: -300px;
            &.open {
                z-index: 12;
                left: 10px;
                margin-top: -20px;
            }
            
        }
        
        &-button {
            @media (min-width:915px) {
                display: none;
            }
            display: flex;
            justify-content: space-between;
            align-items: center;
            border-radius: 35px;
            padding: 11px 13px;
            
            border: 1px solid $grey;
            
            
            
            cursor: pointer;
            .icon {
                &-arrUp {
                    display: inline-block;
                    transform: rotate(90deg);
                }
            }
            
            
        }
        &-block {
            width: 48%;
            &.open {
                &:after {
                    content: '';
                    display: block;
                    position: fixed;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    background: rgba(0,0,0,0.5);
                    z-index: 11;
                }
                .catalog-left-button {
                    background: #fff;
                    position: relative;
                    z-index: 12;
                }
            }
        }
    }
    &-right {
        
    }
    &__filter {
        
        &-block {
            
        }
        &-body {
            
        }
        &-toggle {
            padding-top: 37px;
        }
        &-head {
   
            font-style: normal;
            font-weight: bold;
            font-size: 16px;
            line-height: 19px;
            
            //margin-bottom: 27px;
            
            display: flex;
            justify-content: space-between;
            align-items: center;
            
            cursor: pointer;
            transition: $tr;
            [class^="icon-"] {
                display: inline-block; 
                transform: rotate(-90deg);
                transition: $tr;
            }
            &.close {
                //margin-bottom: 0;
                [class^="icon-"] {
                    transform: rotate(90deg);
                }
            }
            
        }
        &-reset {
            font-style: normal;
            font-weight: bold;
            font-size: 14px;
            line-height: 16px;
            
            text-align: center;
            display: block;
            
            padding: 10px;
            color: $blue;
            text-decoration: none;
        }
    }
    
    &__list {
        flex-wrap: wrap;
        margin-bottom: 58px;
    }
    &__item {
        border: 1px solid #EDEEF2;
        width: calc( 25% - 8px );
        margin-left: 4px;
        margin-right: 4px;
        
        padding: 30px 23px;
        
        font-style: normal;
        font-weight: 300;
        font-size: 14px;
        line-height: 16px;
        text-align: center;
        @media (max-width:700px) {
            width: calc( 50% - 8px );
            padding: 17px 10px;
        }
        &-img {
            max-height: 173px;
            overflow: hidden;
            
            display: flex;
            justify-content: center;
            align-items: center;
        }
        &-image {
            max-width: 100%;
            display: block;
        }
        &-name {
            
            color: $blue;
            text-decoration: none;
            
            display: block;
            width: 100%;
            overflow: hidden;
            text-overflow: ellipsis;
            height: 30px;
            margin-bottom: 26px;
        }
        &-btn {
            padding: 8px 0;
            width: 100%;
            max-width: 136px;
            &:hover {
                border-color: $red;
                background: $red;
                color: #fff;
            }
        }
    }
    &__more {
        text-align: center;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        
        margin-bottom: 70px;
        &-text {
            margin-bottom: 23px;
            
            font-style: normal;
            font-weight: 300;
            font-size: 12px;
            line-height: 16px;
        }
    }
}