.search {
    &-header {
        width: 50%;
        @media (max-width:650px) {
            width: 100%;
            margin-bottom: 10px;
        }
    }
    
    &-input {
        outline: none;
        
            
            margin-right: 10px;
            @media (max-width:900px) {
                margin-right: -50px;
            }
            padding: 16px 14px 15px 14px;
            border: 2px solid $grey;

            font-weight: 100;
            color: $grey;

            border-radius: 35px;
            width: 100%;
        
    }
}
