.partner {
    &__banner {
        background: url(../../images/temp/17.png) no-repeat top center;
        background-size: cover;
        
        color: #fff;
        padding: 110px 60px; 
        margin: 38px 0 120px;
        
        box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
        border-radius: 5px;
        &-h1 {
            font-style: normal;
            font-weight: bold;
            font-size: 36px;
            line-height: 42px;
            margin: 0 0 24px 0;
            padding: 0;
        }
        &-span {
            font-style: normal;
            font-weight: 300;
            font-size: 16px;
            line-height: 19px;
            margin-bottom: 30px;
        }
        .btn {
            display: inline-block;
        }
    }
    &__advantage {
        justify-content: space-around;
        align-items: center;
        margin-bottom: 80px;
        color: $grey;
        &-item {
            align-items: center;
            margin-bottom: 20px;
            max-width: 330px;
        }
        &-icon {
            margin-right: 30px;
        }
        &-name {
            font-style: normal;
            font-weight: bold;
            font-size: 16px;
            line-height: 19px;
            
            margin-bottom: 19px;
        }
        &-info {
            font-style: normal;
            font-weight: 300;
            font-size: 12px;
            line-height: 14px;
        }
    }
    &__info {
        .block-text {
            font-weight: 300;
            font-size: 16px;
            line-height: 19px;
            color: $grey;
        }
        
    }
}