header {
    padding: 15px 0 30px 0;
    background: #FFFFFF;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.05);
    
    position: relative;
    z-index: 20;
    .flex {
        align-items: center;
        justify-content: space-between;
    }
}

.nav {
    @media (max-width:769px) {
        position: absolute;
        top: 0;
        left: -110%;
        right: 110%;
        bottom: 0;
        background: #fff;
        &.open {
            left: 0;
            right: 0;
        }
    }
    &-a {
        font-family: $Roboto;
        font-style: normal;
        font-weight: bold;
        font-size: 14px;
        line-height: 16px;
        
        color: $grey;
        padding: 8px 0;
        margin: 0 15px;
        border-bottom: 1px solid #fff;
        
        display: inline-block;
        text-decoration: none;
        
        margin-bottom: 15px;
        &:hover,
        &.active {
            border-color: $blue;
        }
    }
    &__menu {
        color: $blue;
        text-decoration: none;
        font-size: 30px;
        display: none;
        color: $red;
        
        cursor: pointer;
        @media (max-width:769px) {
            display: block;
        }
    }
}
.tel {
    font-size: 18px;
    font-weight: bold;
    color: $grey;
    
    text-align: right;
    &-a {
        text-decoration: none;
        color: $grey;
        
        display: block;
        
        transition: $tr;
        &:hover {
            color: $red;
        }
    }
    &-span {
        font-weight: 100;
    }
}
.header {
    &-top {
        align-items: flex-end;
        @media (max-width:769px) {
            justify-content: space-between;
            .flex {
                width: 100%;
            }
            .tel {
                display: none;
            }
        }
    }
    &-bottom {
        padding-top: 25px;
        @media (max-width:650px) {
            flex-wrap: wrap;
        }
    }
    @media (max-width:650px) {
        &-button {
            justify-content: space-between;
            display: flex;
            width: 100%;
            .btn {
                width: 48%;
            }
        }
    }
}
.logo {
    margin-bottom: 9px;
    @media (max-width:769px) {
        width: calc( 50% + 80px );
    }
    &-img {
        width: 100%;
        max-width: 160px;
    }
}
