@import "variables";
@import "mixins";
html,
body {
    height: 100%;
    margin: 0;
    padding: 0;
    font-family: $Roboto;
    background: $lgrey;
    
    display: flex;
    flex-direction: column;
}
.main {
    flex: 1 0 auto;
}
body * {
    box-sizing: border-box;
}
.wrapper {
    max-width: 1200px;
    width: 100%;
    margin: 0 auto;
    padding: 0 10px; 
}
.flex {
    display: flex;
}
.text {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 21px;
}
.scroll {
    &-pane {
        width: 100%;
        max-height: 230px;
        overflow: auto;
        outline: inherit;
    }
}