.contact {
    margin-bottom: 110px;
    padding: 43px 40px;
    
    &-in {
        width: 50%;
        @media (max-width:768px) {
            width: 100%;   
        }
    }
    &-head {
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        color: $grey;
        
        margin-bottom: 20px;
    }
    &__block {
        margin-bottom: 40px;
            min-height: 40px;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        
        color: $blue;
    }
    &-tel {
    
        color: $blue;
        
        text-decoration: none;
    }
    @media (max-width:800px) {
        &-hidden {
            display: none;
        }
    }
    @media (max-width:768px) {
        &.block-main {
            background: none!important;
            box-shadow: none!important;
            padding: 0;
            flex-direction: column;
        }
    }
    
    &__form {
        max-width: 350px;
        @media (max-width:768px) {
            max-width: 100%;   
        }
        &-head {
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 19px;
            
            color: $blue;
            
            margin-bottom: 24px;
        }
        &-input {
            border: 2px solid $grey;
            border-radius: 5px;
            
            padding: 11px 13px;
            display: block;
            width: 100%;
            
            margin-bottom: 10px;
            
            outline: none;
            
            background: none;
            
            font-style: normal;
            font-size: 14px;
            line-height: 20px;
            @include plhl {
                color: $grey;
                font-weight: 500;
            }
            @media (max-width:768px) {
                width: 100%;   
                
            }
        }
        &-textarea {
            min-height: 95px;
            margin-bottom: 46px;
            font-family: $Roboto;
        }
        .flex {
            justify-content: flex-end;
            @media (max-width:768px) {
                justify-content: center;
            }
        }
    }
}