.news {
    padding-bottom: 70px;
    &-bread {
        padding-bottom: 0;
    }
    &__block {
        flex-wrap: wrap;
        justify-content: space-between;
    }
    &__item {
        padding: 37px;
        
        @media (min-width:901px) {
            width: 33%;
            &:nth-child(4) {
                width: calc( 50% - 4px );
                & + * {
                    width: calc( 50% - 4px );
                }
            }
        }
        @media (max-width:900px) {
            width: calc( 50% - 4px );

            &:nth-child(3n) {
                width: 100%;
            }
        }
        @media (max-width:550px) {
            width:100%;
            padding: 35px 16px;
            background: none!important;
            box-shadow: none!important;
        }
        @media (min-width:650px) {
            &-img {
                float: right;
            }
        }
        &-name {
            font-weight: bold;
            font-size: 20px;
            color: $blue;
            line-height: 23px;
            letter-spacing: 0.01em;
            
            text-decoration: none;
            margin-bottom: 30px;
            
            display: block;
        }
        &-text {
            font-style: normal;
            font-weight: 300;
            font-size: 14px;
            line-height: 16px;
            display: block;
            
            text-decoration: none;
            
            color: #000;
            
            margin-bottom: 30px;
        }
        &-date {
            font-style: italic;
            font-weight: 300;
            font-size: 16px;
            line-height: 19px;
            margin-bottom: 30px;
        }
        
        &-single {
            width: 100%!important;
        }
        &-nav {
            justify-content: space-between;
            width: 100%;
            padding: 60px 0 30px;
            .btn {
                margin-bottom: 30px;
            }
            @media (max-width:550px) {
                padding: 20px 0;   
                .btn {
                    background: none!important;
                    border: none!important;
                    white-space: normal;
                    &:first-child {
                        padding-left: 0;
                        &:before {
                            content: "\e901";
                            font-family: 'icomoon' !important;
                            speak: none;
                            font-style: normal;
                            font-weight: normal;
                            font-variant: normal;
                            text-transform: none;
                            line-height: 1;
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                            font-size: 13px;
                            margin-right: 17px;
                            color: #A2A8C0 !important;
                            
                            transform: rotate(-180deg);
                        }   
                    }
                    &:last-child {
                        padding-right: 0;
                        text-align: right;
                        &:after {
                            content: "\e901";
                            font-family: 'icomoon' !important;
                            speak: none;
                            font-style: normal;
                            font-weight: normal;
                            font-variant: normal;
                            text-transform: none;
                            line-height: 1;
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                            font-size: 13px;
                            margin-left: 17px;
                            color: #A2A8C0 !important;
                            
                        }   
                    }
                    
                }
            }
        }
    }
}